#member{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/memberBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(70%);
    padding: 225px 0px;

    .inner{
        width: 1180px;
        margin: 0 auto;

        strong{
            position: absolute;
            top: 0;
            left:11%;
            top:12.5%;
            font:bold 250px/1 'Orbitron';
            color:rgba(255,255,255,.05);
            letter-spacing: 7px;
        }

        h2{
            font:bold 85px/1 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 7px;
            text-align: center;
            margin-bottom: 10px;
            margin-left: -60px;
        }

        >p{
            font:normal 25px/1 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 2px;
            text-align: center;
            margin-left: -175px;
        }

        .wrap{
            width: 100%;
            height: 700px;
            padding:150px 0px 100px;
            display: flex;
            justify-content: space-between;
            
            article{
                width: 20%;
                height: 80%;
                position: relative;

                &:nth-child(1){
                    margin-top: -140px;
                }

                &:nth-child(2){

                }

                &:nth-child(3){
                    margin-top: -100px;
                }

                &:nth-child(4){
                    margin-top: -250px;
                }

                img{
                    width: 100%;
                    height: 80%;
                    object-fit: cover;
                    filter: saturate(0);
                    transition: 1s;

                    &:hover{
                        filter:saturate(100%);
                        transform: translateY(-10px);

                    }
                }

                div{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    border:1px solid rgba(255,255,255,.3);
                    top: 30px;
                    left: -30px;
                    z-index:-1;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    padding-left: 30px;

                    h3{ 
                        font:bold 15px/1 'Orbitron';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 5px;
                    }
    
                    p{
                        font:normal 12px/1 'Orbitron';
                        color:rgba(255,255,255,.5);
                        letter-spacing: 1.5px;
                        margin-top: 20px;
                    }
    
                    span{
                        font:normal 10px/1 'Orbitron';
                        color:rgba(255,255,255,.5);
                        letter-spacing: 2.3px;
                        margin-bottom: 15px;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}