#questions{
    width: 100%;
    height: 100vh;
    background-image: url(../../../public/img/mapBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 200px 0px 30px;
    filter: saturate(70%);

    .inner{
        width: 100%;
        height: 100%;

        strong{
            position: absolute;
            top:13%;
            left:4%;
            font:bold 250px/1 'Orbitron';
            color:rgba(255,255,255,.05);
            letter-spacing: 7px;
            z-index:7;
        }

        .wrap{
            width: 1180px;
            margin: 0 auto;
            height: 100%;
            padding:80px 0px 0px;
            display: flex;

            .qnaList{
                width: 50%;
                height: 100%;

                h2{
                    font:bold 85px/1 'Orbitron';
                    color:rgba(255,255,255,.8);
                    letter-spacing: 7px;
                    text-align: center;
                    text-align: left;
                    padding-left: 30px;
                    padding-bottom:30px;
                    border-bottom:0.5px solid #aaa;
                    width: 85%;
                }

                >ul{
                    li{
                        width: 85%;
                        border-bottom:0.5px solid #aaa;

                        .qnaBox{

                            &.on{
                            
                                .qnaBtns{
                                    font:bold 15px/1 'Orbitron';
                                    color:#f9bf7c;

                                    b{
                                        font:bold 20px/15px 'Orbitron';
                                        transform: rotate(90deg);
                                        color:#f9bf7c;
                                    }
                                }

                                p{
                                    display: block;
                                }
                            }

                            .qnaBtns{
                                font:bold 14px/1 'Orbitron';
                                color:rgba(255,255,255,.9);
                                letter-spacing: 2.3px;
                                padding:25px 15px;
                                display: block;
                                cursor: pointer;

                                b{
                                    font:bold 20px/12.5px 'Orbitron';
                                    color:rgba(255,255,255,.9);
                                    margin-right: 10px;
                                    display: inline-block;
                                }
                            }

                            p{
                                font:normal 10px/15px 'Orbitron';
                                color:rgba(255,255,255,.7);
                                margin-bottom: 30px;
                                letter-spacing: 1.3px;
                                padding-left: 35px;
                                display: none;
                            }
                        }
                    }
                }
            }

            .qnaPic{
                width: 50%;
                height: 100%;
                margin-top: 30px;
                padding:75px 0px 0px;
                background-image: url(../../../public/img/ZZZZ.jpg);
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
                opacity: 0.8;
                transition: 1s;

                &:hover{
                    transform: translateY(-45px);
                    opacity: 1;

                    img{
                        &:nth-child(1){animation: qnaAni01 13s;}
                        &:nth-child(2){animation: qnaAni02 11s 1s;}
                        &:nth-child(3){animation: qnaAni03 16s;}
                    }
                }

                img{

                    &:nth-child(1){
                        opacity: 0;
                        position: absolute;
                        width: 15px;
                        height: 15px;
                        z-index:-1;
                    }
        
        
                    &:nth-child(2){
                        opacity: 0;
                        position: absolute;
                        width: 25px;
                        height: 25px;
                        z-index:-1;
                    }
        
                    &:nth-child(3){
                        opacity: 0;
                        position: absolute;
                        width: 20px;
                        height: 20px;
                        z-index:-1;
                    }
                }
            }
        }
    }
}

@keyframes qnaAni01{
    0%{ top: 13%; right: 45%; opacity: 0;}
    10%{opacity: 0.3;}
    100%{top: -26%; right: 30%; opacity: 0;}
}

@keyframes qnaAni02{
    0%{ top: 13%; right: 47%; opacity: 0;}
    20%{opacity: 0.3;}
    100%{top: -24%; right: 32%; opacity: 0;}
}

@keyframes qnaAni03{
    0%{ top: 13%; right: 57.5%; opacity: 0;}
    20%{opacity: 0.3;}
    100%{top: -20%; right: 65.5%; opacity: 0;}
}