@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');

#story{
    width: 100%;
    height: 100%;
    background-image: url(../../../public/img/storyBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: saturate(60%);
    padding-top:100px;
    padding-bottom: 100px;

    .inner{
        width: 1180px;
        height: 100%;
        margin: 0 auto;

        >.svg-inline--fa{
            font-size: 50px;
            color:#eee;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            z-index:10;
            top: 120px;
            opacity: 0.9;
        }

        .wrap{
            width: 100%;
            height: 100%;

            .storyGroup{
                width: 100%;
                height: 600px;
                display: flex;
                overflow: hidden;
                transition: 1.5s;

                &.on{
                    height: 1050px;
                }


                .storyPic{
                    width: 50%;
                    height: 1000px;

                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        filter: saturate(50%);
                    }
                }
        
                .storyContent{
                    height: 1000px;
                    width: 50%;
                    padding:75px 50px;

                    h3{
                        font:bold 30px/35px 'Orbitron';
                        color:rgba(255,255,255,.9);
                        letter-spacing: 5px;
                        margin-bottom: 35px;
                    }

                    p{
                        font:normal 10px/20px 'Orbitron';
                        color:rgba(255,255,255,.7);
                        letter-spacing: 1.7px;
                        margin-bottom: 50px;
                    }

                    strong{
                        font:bold 22.5px/25px 'Dancing Script';
                        color:rgba(255,255,255,.8);
                        letter-spacing: 1.3px;
                        width: 50%;
                        display: block;
                        margin-bottom: 50px;
                    }

                    span{
                        font:bold 10px/1 'Orbitron';
                        color:rgba(255,255,255,.9);
                        letter-spacing: 3px;
                        text-align: right;
                        display: block;
                    }

                    .fa-angle-down{
                        position: relative;
                        font-size: 50px;
                        top: -50.5%;
                        left: 50%;
                        transform: translateX(-50%);
                        color:rgba(255,255,255,.7);
                        cursor: pointer;
                        animation: angleAni 10s;
                        animation-fill-mode: forwards;

                        &:hover{
                            opacity: 1;
                        }

                        &.off{
                            opacity: 0;
                            display: none;
                        }
                    }

                    .fa-angle-up{
                        position: relative;
                        font-size: 50px;
                        left: 50%;
                        transform: translateX(-50%);
                        color:rgba(255,255,255,.7);
                        opacity: 0;
                        display: none;
                        cursor: pointer;
                        transition: 3s;

                        &:hover{
                            opacity: 1;
                        }

                        &.on{
                            opacity: 0.8;
                            display: block;
                        }
                    }
                }
            }
        }
    }
}

@keyframes angleAni {
    0%{opacity: 0;}
    100%{opacity: 0.8;}
}