#news{
    width: 100%;
    padding: 250px 0px;
    background-image: url(../../../public/img/newsBg01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    filter: saturate(70%);
    border:1px solid red;

    .inner{
        width: 1180px;
        margin: 0 auto;
        // border:4px solid yellow;

        strong{
            position: absolute;
            top:9%;
            left:17.7%;
            font:bold 250px/1 'Orbitron';
            color:rgba(255,255,255,.05);
            letter-spacing: 7px;
        }

        h2{
            font:bold 85px/1 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 7px;
            text-align: center;
            margin-bottom: 30px;
            text-align: left;
        }

        ul{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: flex-start;
            // border: 3px solid red;
            padding:100px 0px 50px;

            li{
                width: 48%;
                height: 200px;
                padding: 40px;
                box-shadow: 3px 3px 3px rgba(0,0,0,.9);
                margin-bottom: 20px;
                // background: #1E2831;
                background: #23323b;

                h2{
                    font:bold 18px/1 'Orbitron';
                    letter-spacing: 2px;
                    color:rgba(255,255,255,.9);
                    margin-bottom: 10px;
                }

                p{
                    font:normal 12px/2.3 'Orbitron';
                    letter-spacing: 2.3px;
                    color:rgba(255,255,255,.7);
                }
            }
        }
    }
}